.product {
  padding-top: 88px;
  padding-bottom: 118px;
  background-color: $special-gray;

  @media (min-width: $tablet-width) and (max-width: $max-tablet-width) {
    padding-top: 50px;
    padding-bottom: 32px;
  }

  @media (min-width: $mobile-width) and (max-width: $max-mobile-width) {
    padding-top: 51px;
    padding-bottom: 34px;
  }
}

.product__block {
  display: flex;
  flex-direction: column;
  max-width: 1230px;

  h2 {
    margin: 0;
    margin-bottom: 51px;
    padding: 0;
    font-size: 36px;
    font-family: "RobotoCondensed", "Arial", sans-serif;
    line-height: 140.6%;
    font-weight: 400;
    color: $white;
  }

  @media (min-width: $tablet-width) and (max-width: $max-tablet-width) {
    max-width: unset;

    h2 {
      min-height: 90px;
      margin-bottom: 48px;
      padding-right: 110px;
      font-size: 32px;
    }
  }

  @media (min-width: $mobile-width) and (max-width: $max-mobile-width) {
    max-width: unset;

    h2 {
      min-height: 119px;
      margin-bottom: 40px;
      padding-left: 8px;
      font-size: 28px;
    }
  }
}

.product__list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;

  @media (min-width: $tablet-width) and (max-width: $max-tablet-width) {
    flex-direction: column;
  }

  @media (min-width: $mobile-width) and (max-width: $max-mobile-width) {
    flex-direction: column;
  }
}

.product__item {
  width: 31.6%;
  max-width: 370px;
  min-height: 621px;
  margin-right: 30px;
  margin-bottom: 10px;

  &:nth-of-type(3n) {
    margin-right: 0;
  }

  @media (min-width: $desktop-width) and (max-width: $desktop-mid-width) {
    width: 31%;
  }

  @media (min-width: $tablet-width) and (max-width: $max-tablet-width) {
    width: 100%;
    max-width: unset;
    min-height: 256px;
    margin-right: unset;
    margin-bottom: 46px;
  }

  @media (min-width: $mobile-width) and (max-width: $max-mobile-width) {
    width: 100%;
    max-width: unset;
    min-height: 256px;
    margin-right: unset;
    margin-bottom: 30px;
  }
}

.product__item--production {
  background: url("../img/production-desctop.png") no-repeat;
  background-size: cover;

  @media (min-width: $tablet-width) and (max-width: $max-tablet-width) {
    background: url("../img/production-tablet.png") no-repeat;
    background-size: cover;
  }

  @media (min-width: $mobile-width) and (max-width: $max-mobile-width) {
    background: url("../img/production-mobile.png") no-repeat;
    background-size: cover;
  }
}

.product__item--mounting {
  background: url("../img/mounting-desctop.png") no-repeat;
  background-size: cover;

  @media (min-width: $tablet-width) and (max-width: $max-tablet-width) {
    background: url("../img/mounting-tablet.png") no-repeat;
    background-size: cover;
  }

  @media (min-width: $mobile-width) and (max-width: $max-mobile-width) {
    background: url("../img/mounting-mobile.png") no-repeat;
    background-size: cover;
  }
}

.product__item--components {
  background: url("../img/components-desctop.png") no-repeat;
  background-size: cover;

  @media (min-width: $tablet-width) and (max-width: $max-tablet-width) {
    background: url("../img/components-tablet.png") no-repeat;
    background-size: cover;
  }

  @media (min-width: $mobile-width) and (max-width: $max-mobile-width) {
    background: url("../img/components-mobile.png") no-repeat;
    background-size: cover;
  }
}

.product__link {
  position: relative;
  display: flex;
  z-index: 1;
  width: 100%;
  height: 100%;
  padding: 30px 155px 53px 44px;
  font-size: 20px;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  line-height: 1.5;
  font-weight: 400;
  color: $white;
  transition: $default-transition;
  outline: 0;

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    bottom: 0;
    right: 0;
    width: 63px;
    height: 63px;
    background: url("../img/arrow.png") center center no-repeat;
    background-color: $blue;
  }

  &:focus {
    opacity: 0.5;
  }

  &:hover {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) and (max-width: $max-tablet-width) {
    width: 100%;
    min-height: 256px;
    padding: 20px 520px 43px 44px;
  }

  @media (min-width: $mobile-width) and (max-width: $max-mobile-width) {
    width: 100%;
    min-height: 256px;
    padding: 0 90px 23px 35px;
  }
}
